import { parseNumeral } from '../utils/number'

export const capNumber = (value: number, max: number): string =>
  `${Math.min(max, value)}${value > max ? '+' : ''}`

export const formatNumber = (input: unknown, formatter?: string, defaultValue = ''): string =>
  parseNumeral(input)?.format(formatter) ?? defaultValue

export const shortenNumber = (input?: unknown): string => formatNumber(input, '0a').toUpperCase()

export const formatCurrency = (input?: unknown, defaultValue = ''): string =>
  formatNumber(input, '0,0.00', defaultValue)

export const formatPrefixCurrency = (prefix = '', amount: unknown): string =>
  `${prefix} ${formatCurrency(amount)}`

export const formatInterestRate = (input: number | string): string => formatNumber(input, '0.0[00]')
