import _ from 'lodash'
import numeral, { Numeral } from 'numeral'

export const parseNumeral = (input?: unknown): Numeral | undefined => {
  const _numeral = numeral(input)
  return _.isNumber(_numeral.value()) ? _numeral : undefined
}

export const parseNumber = (input?: unknown): number | undefined =>
  parseNumeral(input)?.value() ?? undefined

export const isNumber = (input?: unknown): boolean => parseNumeral(input) !== undefined
