import { calcDiffDays, parseDate } from '../utils/date'

import { pluralize } from './string'

import type { MomentInput } from 'moment'
import type { LocaleAware } from '../locale'

export type DateFormatKey =
  'YearMonthDayTime' |
  'YearMonthDay' |
  'YearMonth' |
  'Month'
export type DateFormat = LocaleAware<string>
export const DateFormats: { [key in DateFormatKey]: DateFormat } = {
  YearMonthDayTime: { en: 'D MMM YYYY HH:mm', zh: 'YYYY年M月D日 HH:mm' },
  YearMonthDay: { en: 'D MMM YYYY', zh: 'YYYY年M月D日' },
  YearMonth: { en: 'MMM YYYY', zh: 'YYYY年M月' },
  Month: { en: 'MMM', zh: 'M月' },
}

export const formatDate = (input: MomentInput, format = DateFormats.YearMonthDay.en, suffix = ''): string =>
  `${parseDate(input).format(format)}${suffix}`

export const formatNumericDate = (input: string, suffix = ''): string =>
  `${parseDate(input).format('L')}${suffix}`

export const formatDaysLeft = (start: MomentInput, end: MomentInput, label: string): string =>
  pluralize(label, calcDiffDays(start, end))
