import moment, { Moment, MomentInput } from 'moment'

export const parseDate = (input?: MomentInput): Moment => moment.utc(input).utcOffset(8)

export const isToday = (input?: MomentInput): boolean => parseDate().isSame(parseDate(input), 'd')

export const calcDiffDays = (start: MomentInput, end: MomentInput): number =>
  Math.abs(parseDate(start).diff(end, 'd', false))
