import _ from 'lodash'

export const trimAll = (input?: string): string => {
  return _.replace(input ?? '', /\s/g, '')
}

export const maskHkPhoneNumber = (input: string): string => {
  const raw = _.replace(input, /\D/g, '')
  if (raw.length !== 11) return input
  return `+${input.slice(0, 3)} ${input.slice(3, 7)} ****`
}

export const maskEmail = (input: string): string =>
  input.replace(
    /^(.)(.*)(@.*)$/,
    (_, firstLetter, mask, domain) => `${firstLetter}${mask.replace(/./g, '*')}${domain}`
  )

export const maskUserName = (input: string): string =>
  input.replace(
    /^(.{0,3})(.*)$/,
    (_, firstLetter, mask) => `${firstLetter}${mask.replace(/./g, '*')}`
  )

export const extractEndpoint = (input: string): string =>
  input?.replace(/^.+\.com/, '').substr(0, 80) ?? ''

export const splitEnglishName = (englishName = ''): [string, string] => {
  if (_.includes(englishName, ',')) {
    const [lastName = '', firstName = ''] = englishName.split(',')
    return [firstName.trim(), lastName.trim()]
  }
  return ['', englishName.trim()]
}

export const combineEnglishName = (firstName = '', lastName = ''): string => {
  if (!firstName) return lastName.trim()
  if (!lastName) return firstName.trim()
  return `${lastName.trim()}, ${firstName.trim()}`
}
