/**
 * X Character Set – SWIFT Character Set
 * https://www.paiementor.com/swift-formatting-rules-and-character-sets-of-mt-messages/
 */
export const englishAddress = new RegExpValidator(/^(?![-/])[a-zA-Z0-9/\-–?:().,‘'+ ]*$/)

/**
 * HKID check digit algorithm
 * https://zh.wikipedia.org/wiki/%E9%A6%99%E6%B8%AF%E8%BA%AB%E4%BB%BD%E8%AD%89
 */
const isHKID = (str: string): boolean => {
  const strValidChars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
  // basic check length
  if (str.length < 8) return false
  // handling bracket
  if (str.charAt(str.length - 3) === '(' && str.charAt(str.length - 1) === ')') {
    str = str.substring(0, str.length - 3) + str.charAt(str.length - 2)
  }
  // convert to upper case
  str = str.toUpperCase()
  // internal regular ，regular expression to check pattern and split
  const hkIDPat = /^([A-Z]{1,2})([0-9]{6})([A0-9])$/
  const matchArray = str.match(hkIDPat)
  // not match, return false
  if (matchArray === null) return false
  // the character part, numeric part and check digit part
  const [, charPart, numPart, checkDigit] = matchArray
  let checkSum = 0
  if (charPart.length === 2) {
    checkSum += 9 * (10 + strValidChars.indexOf(charPart.charAt(0)))
    checkSum += 8 * (10 + strValidChars.indexOf(charPart.charAt(1)))
  } else {
    checkSum += 9 * 36
    checkSum += 8 * (10 + strValidChars.indexOf(charPart))
  }
  // calculate the checksum for numeric part
  let i = 0
  let j = 7
  for (i = 0, j = 7; i < numPart.length; i++, j--) {
    checkSum += j * _.toNumber(numPart.charAt(i))
  }
  // verify the check digit
  const remaining = checkSum % 11
  const verify = remaining === 0 ? 0 : 11 - remaining
  return verify === _.toNumber(checkDigit) || (verify === 10 && checkDigit === 'A')
}
export const hkid = new CustomValidator(isHKID)
