import _ from 'lodash'
import _pluralize from 'pluralize'

import { trimAll } from '../utils/string'
import { phoneNumber } from '../validator'

export const pluralize = (input: string, count: number): string => _pluralize(input, count, true)

export const formatEmail = (input?: string): string => {
  return _.toLower(trimAll(input))
}

export const formatHkPhoneNumber = (input: string): string => {
  if (!phoneNumber.validate(input)) return input
  return `+852 ${input.substr(0, 4)} ${input.substr(4)}`
}

export const formatUsername = (input?: string): string => {
  return _.toLower(trimAll(input))
}

export const formatLastName = (lastName = ''): string => {
  return _.toUpper(lastName)
}

export const formatFirstName = (firstName = ''): string => {
  return _.map(_.split(firstName, ' '), w => _.capitalize(w)).join(' ')
}

export const formatAccountNumber = (input: string): string => {
  if (!_.isString(input) || input.length !== 10) return input
  return `${input.substring(0, 3)} - ${input.substring(3, 6)} - ${input.substring(6)}`
}
